.ContactData{
    margin: 20px auto;
    width: 80%;
    text-align: center;
    box-sizing: border-box;
    box-shadow: 0 2px 3px #ccc;
    border: 1px solid #eee;
    padding: 10px;
}
.Input{
    display: block;
}
button:disabled{
    color: gray;
    cursor:not-allowed;
}
@media (min-width: 600px) {
    .ContactData{
        width: 500px;
    }
}
